const Bookings = {
  async render() {
    try {
      const id = window.location.hash.split("/")[2];
      const response = await fetch(`http://localhost:3000/events/${id}`);

      if (!response.ok) {
        throw new Error("Failed to fetch event data");
      }

      const event = await response.json();

      return ` 
        <br />

        <section class="signup-image">
          <div class="signup-container">
            <h1>BOOKING</h1>
            <p class="des">${event.title}</p>

            <form action="" class="signup-container__signup-field">
              <input type="text" value="${event.id_booking}" placeholder="Ini nanti muncul id_booking-nya" required disabled>
              <input type="text" placeholder="Input your name" required>
              <input type="text" placeholder="Input your username" required>
              <input type="email" placeholder="Input your email" required>
              <input type="date" placeholder="Input your date" required>
              <div class="buttons">
                <button type="submit" class="login-btn">Booking</button>
                <button type="button" class="signup-btn">Cancel</button>
              </div>
            </form>
          </div>
        </section>
      `;
    } catch (error) {
      console.error("Error fetching event data:", error);
      return `<p>Error fetching event data. Please try again later.</p>`;
    }
  },

  async afterRender() {
    const header = document.querySelector(".app-bar");
    if (header) {
      header.style.display = "none";
    }
  },
};

export default Bookings;
